import React from "react";
import { Card, Box, Icon, styled } from "@mui/material";
import { Themecolors } from "api/Colors";

const StyledCard = styled(Card)(({ theme }) => ({
  width: "auto",
  height: "100px",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  padding: "20px !important",
  cursor: "pointer",
  background: "rgb(244 246 248)",
  transition: "transform .2s",
  "&:hover": {
    transform: "scale(1.02)",
    backgroundImage: Themecolors.DB_hv3,
  },
  [theme.breakpoints.down("sm")]: { padding: "16px !important" },
}));

const ContentBox = styled(Box)<{ hasIcon: boolean }>(({ theme, hasIcon }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: hasIcon ? "space-between" : "center",
  width: "100%",
  "& .icon": {
    opacity: 0.8,
    fontSize: "48px",
    color: Themecolors.DB_bg1,
  },
}));

const Heading = styled("h6")<{ hasIcon: boolean }>(({ theme, hasIcon }) => ({
  fontSize: "18px",
  fontWeight: "600",
  color: Themecolors.DB_text2,
  textAlign: hasIcon ? "left" : "center",
  flex: 1,
  marginLeft: "10px",
}));

interface StyledCardComponentProps {
  icon?: React.ReactNode;
  title: string;
  onClick: () => void;
}

const StyledCardComponent: React.FC<StyledCardComponentProps> = ({
  icon,
  title,
  onClick,
}) => {
  const hasIcon = !!icon;

  return (
    <StyledCard onClick={onClick}>
      <ContentBox hasIcon={hasIcon}>
        {hasIcon && <Icon className="icon">{icon}</Icon>}
        <Heading hasIcon={hasIcon}>{title}</Heading>
      </ContentBox>
    </StyledCard>
  );
};

export default StyledCardComponent;
